import React from 'react'
import ReactDOM from 'react-dom/client'
import { BrowserRouter } from 'react-router-dom'
import App from './App'

//   - 
import { Monitor, ErrorBoundary } from 'mini-sentry-sdk'

// 
const originalFetch = window.fetch;
window.fetch = function (...args) {
  console.log(' :', args[0]);
  if (args[1] && args[1].body) {
    console.log(' :', args[1].body);
  }
  return originalFetch.apply(this, args)
    .then(response => {
      console.log(' :', args[0], response.status);
      return response;
    })
    .catch(error => {
      console.error(' :', args[0], error);
      throw error;
    });
};

console.log('  Mini Sentry SDK...');

//  SDK
const monitor = new Monitor({
  projectId: '1',
  apiUrl: 'http://192.168.1.161:8080/frontend', //  API URL
  debug: true, // 
  sampleRate: 1, // 100% 
  maxBreadcrumbs: 20, // 
  defaultTags: {
    environment: 'test',
    version: '1.0.0-test',
    testMode: true
  },
  enabledModules: {
    error: true,
    performance: true,
    behavior: true
  }
});

console.log(' Monitor :', monitor);

// 
try {
  monitor.init();
  console.log(' Monitor ');
} catch (error) {
  console.error(' Monitor :', error);
}

//  monitor  window 
(window as any).monitor = monitor;

// 
console.log(' Mini Sentry SDK !');

//  SDK 
try {
  const status = monitor.getStatus();
  console.log(' :', status);
} catch (error) {
  console.error(' :', error);
}

console.log('  window.monitor ');

// 
console.log(' ...');
fetch('http://localhost:3001/health')
  .then(res => res.json())
  .then(data => {
    console.log(' :', data);

    // 
    console.log(' ...');

    // 
    try {
      monitor.trackEvent('navigation', '', {
        timestamp: Date.now(),
        userAgent: navigator.userAgent,
        url: window.location.href
      });
      console.log(' trackEvent ');
    } catch (error) {
      console.error(' trackEvent :', error);
    }

    // 
    try {
      monitor.trackMetric('app_startup_time', Date.now(), {
        phase: 'initialization'
      });
      console.log(' trackMetric ');
    } catch (error) {
      console.error(' trackMetric :', error);
    }

    // 
    setTimeout(() => {
      try {
        monitor.captureError(new Error(''));
        console.log(' captureError ');
      } catch (error) {
        console.error(' captureError :', error);
      }
    }, 1000);

  })
  .catch(error => {
    console.error(' :', error);
    console.log(' Mock  http://localhost:3001');
  });

ReactDOM.createRoot(document.getElementById('root')!).render(
  <React.StrictMode>
    <ErrorBoundary
      projectId="TEST-PROJECT-001"
      onError={(error: any) => {
        console.log(' ErrorBoundary :', error);
        try {
          monitor.captureError(new Error(error.message));
          console.log(' ErrorBoundary ');
        } catch (e) {
          console.error(' ErrorBoundary :', e);
        }
      }}
      fallback={
        <div style={{ padding: '20px', textAlign: 'center', color: 'red' }}>
          <h2> </h2>
          <p>ErrorBoundary </p>
          <button onClick={() => window.location.reload()}>
             
          </button>
        </div>
      }
    >
      <BrowserRouter>
        <App />
      </BrowserRouter>
    </ErrorBoundary>
  </React.StrictMode>
)