import React, { useState } from 'react'
import { Routes, Route, Link, useNavigate } from 'react-router-dom'
import ErrorTest from './components/ErrorTest'
import PerformanceTest from './components/PerformanceTest'
import BehaviorTest from './components/BehaviorTest'

function HomePage() {
  const navigate = useNavigate();
  const [stats, setStats] = useState<any>(null);

  //  Mock 
  const fetchStats = async () => {
    try {
      const response = await fetch('http://localhost:3001/api/debug/stats');
      const data = await response.json();
      setStats(data);
    } catch (error) {
      console.error(':', error);
    }
  };

  // 
  const clearStats = async () => {
    try {
      await fetch('http://localhost:3001/api/debug/clear', { method: 'POST' });
      setStats(null);
      alert(' ');
    } catch (error) {
      console.error(':', error);
    }
  };

  return (
    <div className="test-section">
      <h1> Mini Sentry SDK </h1>

      <div style={{ marginBottom: '20px' }}>
        <h3> </h3>
        <button className="test-button" onClick={fetchStats}>
          
        </button>
        <button className="test-button" onClick={clearStats}>
          
        </button>

        {stats && (
          <div className="test-status info">
            <strong>:</strong><br />
            : {stats.errors}  |
            : {stats.performance}  |
            : {stats.behavior}  |
            Sourcemap: {stats.sourcemaps} 
          </div>
        )}
      </div>

      <div style={{ marginBottom: '20px' }}>
        <h3> </h3>
        <Link to="/error-test">
          <button className="test-button danger"></button>
        </Link>
        <Link to="/performance-test">
          <button className="test-button"></button>
        </Link>
        <Link to="/behavior-test">
          <button className="test-button"></button>
        </Link>
      </div>

      <div style={{ marginBottom: '20px' }}>
        <h3> </h3>
        <button
          className="test-button"
          onClick={() => {
            (window as any).monitor?.trackEvent('user', '');
          }}
        >
          
        </button>

        <button
          className="test-button"
          onClick={() => {
            (window as any).monitor?.trackMetric('custom_metric', Math.random() * 100);
          }}
        >
          
        </button>

        <button
          className="test-button"
          onClick={() => {
            (window as any).monitor?.captureError(new Error(''));
          }}
        >
          
        </button>
      </div>

      <div style={{ marginBottom: '20px' }}>
        <h3> </h3>
        <button
          className="test-button"
          onClick={() => fetch('/api/test/success').catch(console.error)}
        >
          
        </button>

        <button
          className="test-button danger"
          onClick={() => fetch('/api/test/404').catch(console.error)}
        >
          404 
        </button>

        <button
          className="test-button danger"
          onClick={() => fetch('/api/test/500').catch(console.error)}
        >
          500 
        </button>
      </div>

      <div>
        <h3> </h3>
        <ul style={{ textAlign: 'left', lineHeight: '1.6' }}>
          <li> <strong></strong>: JavaScript</li>
          <li> <strong></strong>: </li>
          <li> <strong></strong>: </li>
          <li>  Network </li>
          <li>  Console  SDK </li>
          <li>  Mock </li>
        </ul>
      </div>
    </div>
  );
}

function About() {
  return (
    <div className="test-section">
      <h2></h2>
      <p></p>
      <Link to="/">
        <button className="test-button"></button>
      </Link>
    </div>
  );
}

function App() {
  return (
    <div>
      {/*  */}
      <nav style={{
        background: '#001529',
        padding: '16px',
        color: 'white',
        marginBottom: '0'
      }}>
        <h1 style={{ margin: 0, fontSize: '18px' }}>
           Mini Sentry SDK 
        </h1>
        <div style={{ marginTop: '8px' }}>
          <Link to="/" style={{ color: '#1890ff', marginRight: '16px' }}></Link>
          <Link to="/about" style={{ color: '#1890ff', marginRight: '16px' }}></Link>
          <Link to="/error-test" style={{ color: '#1890ff', marginRight: '16px' }}></Link>
          <Link to="/performance-test" style={{ color: '#1890ff', marginRight: '16px' }}></Link>
          <Link to="/behavior-test" style={{ color: '#1890ff' }}></Link>
        </div>
      </nav>

      {/*  */}
      <Routes>
        <Route path="/" element={<HomePage />} />
        <Route path="/about" element={<About />} />
        <Route path="/error-test" element={<ErrorTest />} />
        <Route path="/performance-test" element={<PerformanceTest />} />
        <Route path="/behavior-test" element={<BehaviorTest />} />
      </Routes>

      {/*  */}
      <footer style={{
        background: '#f0f0f0',
        padding: '16px',
        textAlign: 'center',
        marginTop: '40px',
        fontSize: '12px',
        color: '#666'
      }}>
        <p>
           :  Network  Console  SDK 
          <br />
           Mock : http://localhost:3001
        </p>
      </footer>
    </div>
  );
}

export default App